<template>
  <div class="layout">
    <div class="header">
      <div class="back" @click="back">返回</div>
      <div class="search">
        <input class="searchinput" type="text" v-model="keywords" @change="searchbooks" />
        <img src="../../imgs/search.png" alt class="searchicon" @click="searchbooks" />
      </div>
    </div>
    <div class="content">
      <div v-if="keywords==''||searchlist.length==0" class="recommend">
        <div class="panel">
          <div class="panel-title">男生必看</div>
          <div class="panel-content">
            <div v-for="item in manlist" :key="item._id" class="book" @click="bookinfo(item._id)">
              <img :src="item.cover" class="bookcover" />
              <div class="bookinfo">
                <div class="bookname">{{item.title}}</div>
                <div class="booktag">{{item.majorCate}}</div>
                <div class="bookscore">{{item.rating.score}}分</div>
              </div>
            </div>
          </div>
        </div>
        <div class="panel">
          <div class="panel-title">女生热门</div>
          <div class="panel-content">
            <div v-for="item in womenlist" :key="item._id" class="book" @click="bookinfo(item._id)">
              <img :src="item.cover" class="bookcover" />
              <div class="bookinfo">
                <div class="bookname">{{item.title}}</div>
                <div class="booktag">{{item.majorCate}}</div>
                <div class="bookscore">{{NumtoFixed(item.latelyFollower)}}万热度</div>
              </div>
            </div>
          </div>
        </div>

        <div class="panel">
          <div class="panel-title">主编力荐</div>
          <div class="panel-content">
            <div v-for="item in otherlist" :key="item._id" class="book" @click="bookinfo(item._id)">
              <img :src="item.cover" class="bookcover" />
              <div class="bookinfo">
                <div class="bookname">{{item.title}}</div>
                <div class="booktag">{{item.majorCate}}</div>
                <div class="bookscore">{{NumtoFixed(item.latelyFollower)}}万热度</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="searchlist">
        <div class="panel">
          <div class="panel-title">{{"共计"+searchlist.length+"条记录"}}</div>
          <div class="panel-content">
            <div
              v-for="item in searchlist"
              :key="item._id"
              class="searchitem"
              @click="bookinfo(item._id)"
            >
              <img :src="getrealeurl(item.cover)" class="bookcover" />
              <div class="iteminfo">
                <div class="itemname">{{item.title}}</div>
                <div class="itemintro">{{item.shortIntro}}</div>
                <div class="itemcat">{{item.cat}}</div>
                <div class="itemauthor">{{item.author}}分</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  data () {
    return {
      manlist: [],
      womenlist: [],
      otherlist: [],
      keywords: '',
      searchlist: []
    }
  },
  methods: {
    NumtoFixed (num) {
      return (num / 10000).toFixed(1);
    },
    getrealeurl (url) {
      return '//statics.zhuishushenqi.com/agent/' + encodeURIComponent(url.replace('/agent/', ''));
    },
    back () {
      this.$router.back();
    },
    bookinfo (bookid) {
      this.$router.push({ path: '/Book', query: { bookid } })
    },
    searchbooks () {
      if (this.keywords !== '') {
        axios.get('http://novel.kele8.cn/search?keyword=' + this.keywords).then((res) => {
          // console.log(res);
          this.searchlist = res.data.books;
        });
      }
    }
  },
  mounted () {
    axios.get('https://b.zhuishushenqi.com/category/page?node=&sex=male&page=1&groupid=&pl=ios&tabType=jx').then((result) => {
      // console.log(result);
      this.manlist = result.data.data[3].books.slice(0, 6);
      this.womenlist = result.data.data[4].books.slice(0, 6);
      this.otherlist = result.data.data[5].books.slice(0, 6);
      parseFloat()
    })
  }
}
</script>
